.header {
    background-color: #212121;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 13px 0;
    z-index: 18;
    height: 76px;
    &__icon {}
    &__right{
        display: flex;
        align-items: center;
    }
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__text {
        color: #FFF;
        font-size: 30px;
        font-weight: 300;
        margin: 0 0 0 20px;
        span {
            font-weight: 700;
            color: #FFF;
        }
        b {
            color: #EA5257;
            font-weight: 700;
        }
    }
    &__tel {
        font-weight: 400;
        color: #FFF;
        font-family: "Arial";
        font-size: 24px;
        margin-bottom: 6px;
        span {
            color: #EA5257;
        }
    }
    &__info {
        position: relative;
        margin-right: 50px;
        &:before {
            position: absolute;
            left: -52px;
            top: 3px;
            content: '';
        }
    }
    &__call {
        font-weight: 400;
        color: #FFF;
        font-size: 14px;
    }
    &__logo {
        display: flex;
        align-items: center;
        margin-top: 2px;
    }
    &__buter {
        cursor: pointer;
        height: 25px;
        span {
            display: block;
            background-color: #FFF;
            width: 33px;
            height: 3px;
            margin-bottom: 8px;
            transition: all 300ms ease 0s;
            &:last-child {
                margin-bottom: 0;
            }
        }
        &.is-active {
            margin-top: 18px;
            span {
                &:first-child {
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    display: none;
                }
                &:last-child {
                    transform: rotate(-45deg);
                    margin-top: -11px;
                }
            }
        }
    }
    &__menu {
        opacity: 0;
        visibility: hidden;
        transition: all 300ms ease 0s;
        position: absolute;
        top: 0;
        right: 0;
        width: 280px;
        height: 100vh;
        z-index: 101;
        background-color: rgba(0, 2, 2, .84);
        transform: translateX(100%);
        padding: 20px;
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
        overflow: auto;
        &.is-open {
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
        }
    }
    .menu {
        &__link {
            color: #FFF;
            font-size: 16px;
            line-height: 22px;
            font-weight: 700;
            position: relative;
            &:hover {
                &:before,
                &:after {
                    opacity: 1;
                }
            }
            &:hover {
                color: #EA5257;
            }
        }
        &__item {
            margin-bottom: 20px;
        }
        &__close {
            cursor: pointer;
            height: 25px;
            position: absolute;
            top: 32px;
            right: 20px;
            span {
                display: block;
                background-color: #FFF;
                width: 33px;
                height: 3px;
                margin-bottom: 8px;
                transition: all 300ms ease 0s;
                &:first-child {
                    transform: rotate(45deg);
                }
                &:last-child {
                    transform: rotate(-45deg);
                    margin-top: -11px;
                    margin-bottom: 0;
                }
            }
        }
    }
}