@import "components/header";
.home{
	background-image: url('../img/home-bg.png');
	background-position: center 0;
	background-size: cover;
	min-height: 705px;
	position: relative;
	&:before {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(0,0,0,.65);
	}
	&__container{
		position: relative;
		z-index: 1;
	}
	&__bottom-button{
		background-color: transparent;
		padding: 0;
		border: 0;
		width: 47px;
		height: 47px;
		
		.svg-icon{
			width: 100%;
			height: 100%;
			fill: #FFF;
		}
	}
}
