ul li{
	list-style: none;
}
img{
	max-height: 100%;
}
body {
  	background: #FFFFFF;
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 14px;
}
.out {
	min-width: 320px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
.wrapper {
	max-width: 1180px;
	margin: auto;
	padding: 0 20px;
}
.content {
	flex: 1 0 auto;
	padding-top: 76px;
}
.footer{
    flex: 0 0 auto;
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {font-family: 'Oswald', sans-serif;}
h1, .h1 {font-size:2em; margin:0.67em 0}
h2, .h2 {font-size: 1.8em; margin: 0.37em 0}
h3, .h3 {font-size: 1.5em; margin: 0.17em 0}
p{
	line-height: 21px;
	margin-bottom: 15px;
}
.overlay{
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: rgba(0,0,0,.7);
	position: fixed;
	opacity: 0;
	visibility: hidden;
	z-index: 100;
	transition: all 300ms ease-in-out;
	&.is-visible{
		opacity: 1;
		visibility: visible;
	}
}
table {
    width: 100%;
    background-color: #f0f0f0;
    border: 3px solid #34b8c0;
    margin-bottom: 15px;
	line-height: 1.42857143;
}

table td, table th {
    border: 1px solid #cccccc;
    padding: 2px
}

table th {
    color: #6f51c7;
}
